body{
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}
::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
}

.store{
    z-index: 3;
    position: relative;
    display: flex;
    width: 100px;
    height: 100px;
}

.fa-store{
    font-size: 40px;
    color:white;
    text-align: center;
    border-radius: 20px;
    position: fixed;
    bottom: 30px;
    right: 30px;
   
    opacity: 1;
    z-index: 5;

}