
.profile{
    margin-top: 10px;
    margin-left: 50px;
}

.profile_top{
    display: flex;
    align-items: center;
    flex:1;
}

.profile_topDetails>h1{
    font-size: 25px;
}

.profile_topDetails>h3{
    font-size: 15px;
}

.profile_top>img{
    object-fit:contain;
    height: 70px;
    margin: 30px 15px 30px 30px;
    border: 2px solid #35353a;
    border-radius: 50%;
    padding: 3px;
}

.bottomLine{
    display: flex;
}

/*커스텀*/
.profile_top>img:hover{
    border-color: purple;
}

.profile_icon_button{
    display:flex;
    margin-left:720px;
    font-size: 30px;
    align-items: center;
}

.followButton{
    margin-left: 5px;
    text-align: center;
    background-color: rgb(53, 53, 53);
    font-size: 15px;
    width: 90px;
    height: 40px;
    border-radius: 8px;
    line-height: 40px; /*height와 line-height를 같게 하면 수직 중앙정렬이 된다.*/
}

.followButton:hover{
    cursor: pointer;
}

.profile_icon_button>i{
    cursor: pointer;
    margin: 8px;
    padding: 10px;
    border-radius: 8px;
    width: 20px;
    height: 20px;
    text-align: center;
    font-size: 16px;
    line-height: 20px;
}

.profile_icon_button>.fa-heart:hover{
    color:rgb(253, 75, 104);
}


.profile_icon_button>.graybg{
    background-color: rgb(53, 53, 53);
}



.profile__menu{
    display:flex;
    margin-left:40px;
    margin-bottom:20px
}

.profile__menu>h3{
    margin-right: 15px;
    cursor: pointer;
}

.profile__menu>.active {
    color: #a970ff;
    border-bottom: 5px solid #a970ff;
    padding-bottom: 5px;
}


.profile_frame{
    margin-left:40px;
    display: flex;
}

.profile_MainVideo{
    
}

.profile_MainVideo>iframe{
    width: 700px;
    height:400px;
}

.profile_introduction{
    margin-left: 50px;
    width:400px;
    height:555px;
    background-image: url("https://images.unsplash.com/photo-1580234811497-9df7fd2f357e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1167&q=80");
    background-color: rgb(249, 248, 255);
    background-blend-mode: multiply;
    filter:grayscale(70%);
    box-shadow: 0px 5px 5px 0px black;
    border-style:inset;
    border-width: 5px;
    border-color: white;
    box-sizing: border-box; /* border까지 크기에 포함 */
}


.profile_square{
    display:flex;
    width:700px;
    height: 150px;
    background-color: rgb(44, 44, 48);
    box-shadow: 0px 5px 5px 0px black;
    align-items: center;
}

.profile_quareBox{
    margin: 62px;
}



.sqaure__verticalLine{
    border-left: 1px solid #4a4a4c;
    height: 8rem;
}

.profile_intro{
    
    opacity: 80%;
    margin:10px;
    height:50px;
    background-color: #35353a;
    border-width: 2px;
    border-style: ridge;
    border-color: white;
    box-sizing: border-box; /* border까지 크기에 포함 */
}
.profile_introBox{
    opacity: 80%;
    margin:10px;
    height:200px;
    background-color: #35353a;
    border-width: 2px;
    border-style: ridge;
    border-color: white;
    box-sizing: border-box; /* border까지 크기에 포함 */
}

.profile_messageBox{
    display: flex;
    margin:20px 10px 10px 10px;
    text-align: center;
}

.messageBox_message{
    opacity: 80%;
    width:50%;
    height:120px;
    background-color: #35353a;
    box-sizing: border-box; /* border까지 크기에 포함 */
}

.messageBox_offer{
    opacity: 80%;
    width:50%;
    height:120px;
    background-color: #35353a;
    box-sizing: border-box; /* border까지 크기에 포함 */
}

.messageBox_center{
    margin:5px;
}