.header{
    height: 3.5rem;
    border-bottom: 1pix solid black;
    display: flex; /*가로방향 배치, 높이 자동 설정*/
    align-items: center;
    background-color: #18181b;
    z-index: 100;
    position: sticky;
    top: 0;
}

.header__left {
    display:flex;
    align-items: center;
    flex: 1; /*할당되는 모든 공간 차지*/
}

.header__leftLogo{
    object-fit: contain; /*가로세로 비 유지, 콘텐츠 박스 내부로 들어가도록 크기 조절*/
    height: 2.5rem;
    margin-left: 20px;
}

.header__left>h2{
    margin:20px;
    font-size:18px; 
}

.header__verticalLine{
    border-left: 1px solid #4a4a4c;
    height: 3rem;
}

.header__left>i{
    margin-left: 15px;
}

.header__center{
    background-color: #4a4a4c;
    height: 2.2rem;
    display: flex;
    width: 25%;
    border-radius: 8px;
    overflow: hidden;
    align-items: center;
}

.header__center:hover{
    outline: 2px solid rgb(136, 135, 218);
}

.header__center>input{
    outline:none;
    background-color: transparent;
    flex:1;
    margin-left:15px;
    font-size: large;
    border:none;
}

.header__centerLogoContainer{
    background-color: #2b2b2d;
    width:50px;
    height:100%;
    display:flex;
    align-items: center;
    justify-content: center;
}

.header__right{
    display:flex;
    flex:1;
    justify-content: flex-end; /*플렉스 요소의 수평 방향 정렬 방식 (flex-end:컨테이너 뒤쪽에서부터 배치)*/
}

.header__rightContainer{
    display:flex;
    justify-self: space-between; /*space between : 요소들 사이에만 여유공간을 두고 배치*/
    align-items: center;
}

.header__rightContainer>i {
    margin:15px;
}

.myProfileIcon{
    margin:20px;
}

.header__ticket{
    margin: 10px;
    display:flex;
    align-items: center;
    background-color: #4a4a4c;
    height: 2.1rem;
    padding-left: 20px;
    padding-right:20px;
    border-radius: 5px;
    font-size: 14px;
}

.header__ticket>i{
    margin-right:5px;
}
