.Mainitem{
    margin-top: 10px;
    display:flex;
    flex-direction: column;
    width: 300px;
    height: 270px;
    overflow:hidden;
    white-space:nowrap;
    margin-right: 20px;
}

.item__details{
    display:flex;
    margin-top:15px;
}

.item__details>img{
    object-fit: contain;
    height:60px;
    margin-right:10px;
}

.itemThumbnail {
    font-family: 'Raleway', Arial, sans-serif;
    color: #fff;
    position: relative;
    overflow: hidden;
    min-width: 230px;
    max-width: 315px;
    max-height: 220px;
    text-align: left;
    background-color: #07090c;
    font-size: 16px;
    -webkit-perspective: 50em;
    perspective: 50em;
    
  }
  .itemThumbnail * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.6s ease;
    transition: all 0.6s ease;
  }
  .itemThumbnail img {
    opacity: 1;
    width: 100%;
    -webkit-transform-origin: 50% 0%;
    -ms-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
  }
  .itemThumbnail figcaption {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    -webkit-transform: rotateX(90deg);
    transform: rotateX(90deg);
    -webkit-transform-origin: 50% 100%;
    -ms-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    z-index: 1;
    opacity: 0;
    padding: 20px 30px;
  }
  .itemThumbnail h3,
  .itemThumbnail p {
    line-height: 1.5em;
  }
  .itemThumbnail h3 {
    margin: 0;
    font-weight: 800;
    text-transform: uppercase;
  }
  .itemThumbnail p {
    font-size: 0.8em;
    font-weight: 500;
    margin: 0 0 15px;
  }
  .itemThumbnail .read-more {
    border: 2px solid #ffffff;
    padding: 0.5em 1em;
    font-size: 0.8em;
    text-decoration: none;
    color: #ffffff;
    display: inline-block;
  }
  .itemThumbnail .read-more:hover {
    background-color: #ffffff;
    color: #000000;
  }
  .itemThumbnail:hover img,
  .itemThumbnail.hover img {
    -webkit-transform: rotateX(-90deg);
    transform: rotateX(-90deg);
    opacity: 0;
  }
  .itemThumbnail:hover figcaption,
  .itemThumbnail.hover figcaption {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
    opacity: 1;
    -webkit-transition-delay: 0.2s;
    transition-delay: 0.2s;
  }