.sidebar{
    padding: 10px;
    width: 11%;
    background-color: #1f1f23;
    display:flex;
    flex-direction: column;
    position:fixed;
    height:100%;
}


.sidebar__top>h5{
    margin-top:25px;
    margin-bottom:25px;
}

.sidebar__topShowMore{
    color:#6686f6;
    cursor: pointer;
}

.sidebar__bottom{
    position:fixed;
    bottom:0;
    height:4rem;
    display:flex;
    align-items: center;
    justify-content: center; /*플렉스 요소 가운데 부터 배치*/
    border-top: 1px solid #4f4f52;
}

.sidebar__battomContaier{
    background-color: #4f4f52;
    width:90%;
    display:flex;
    height:2.5rem;
    border-radius: 8px;
    align-items:center;
    padding-left:10px;
    padding-right: 5px;
}